import React, { useEffect, useState } from 'react';
import Iframe from 'react-iframe';
import data from '../core/data';

const style = {
    titleBullet: { width: "2em", height: "0.3em", backgroundColor: "#000", marginTop: "2.7rem" },
    h2: { borderBottom: "1px solid #999", marginTop: "0.2em", paddingBottom: "0.2em" }
}

const PageBlogpost = () => {
    const [isData, setisData] = useState(false);
    useEffect(() => {
        const decryptQuery = data.parseQuery();
        const cid = (decryptQuery && decryptQuery.cid) ? decryptQuery.cid : '';
        const tpid = (decryptQuery && decryptQuery.tpid) ? decryptQuery.tpid : '';
        data.getTopicJSON(cid, tpid).then((result: any) => {
            data.topicJSON = result.data;
            setisData(true);
        });
    }, []);

    return (
        <div>
            {isData && <PlayerMain />}
        </div>
    );
};

const PlayerMain = () => {

    const hash = window.location.hash;
    const query = hash.split('?d=')[1];

    return (
        <div>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <div style={style.titleBullet}></div>
            <h2 style={style.h2}>오늘의 주요 단어 및 표현</h2>
            <TodayExpression />
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <div style={style.titleBullet}></div>
            <h2 style={style.h2}>영어로 먼저 생각해 보아요</h2>
            <PlayerLists lang="ko" />
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <div style={style.titleBullet}></div>
            <h2 style={{ marginTop: "0.2em", paddingBottom: "0.2em", marginBottom: "0" }}>영어로 들어볼까요?</h2>
            <Iframe url={"https://casete.in/#/minip?d=" + query}
                height={String(30 + (29 * data.topicJSON.sentense.length))}
                width="100%"
                position="relative"
                frameBorder={0}
                // styles={{ border: '0' }}
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            />
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <div style={style.titleBullet}></div>
            <h2 style={style.h2}>아래 영어 문장을 입에 익숙해질 때까지 낭독해보세요.</h2>
            <PlayerLists lang="en" />
            <p>&nbsp;</p>
            <p>&nbsp;</p>
            <p>&nbsp;</p>

        </div>
    );
}

const PlayerLists = ({ lang }: { lang: string }) => {
    const SentenseList = data.topicJSON.sentense.map((item: any, i: number) => {
        return <Sentense info={item} key={i} idx={i} lang={lang} />
    })
    return (<ul style={{ paddingLeft: "20px" }}>
        {SentenseList}
    </ul>);
}

const Sentense = ({
    info, idx, lang
}: {
    info: any, idx: number, lang: string
}) => {
    const words = info.words;
    return (
        <>
            <li style={{ display: "flex" }}>
                <div style={{ marginRight: "15px" }}>{idx + 1}.</div>
                <div>{info[lang]}</div>
            </li>
            {lang === 'en' && words && <WordList json={words} mode="ensentense" />}
        </>
    );
}

const WordList = ({
    json,
    mode,
}: {
    json: any,
    mode: string,
}) => {
    const Itm = json.map((item: string[], i: number) => {
        return <li>
            <span><i>* {item[0]}</i></span>
            <span> : </span>
            <span><i>{item[1]}</i></span>
        </li>
    })
    return (
        <ul style={{ fontSize: "0.9em", marginLeft: "17px", marginBottom: `${mode === 'ensentense' ? 10 : 0}px` }}>
            {Itm}
        </ul>
    );
}

const TodayExpression = () => {
    // const Expression:any = [];
    const Expression = data.topicJSON.sentense.map((item: any, i: number) => {
        const words = item.words;
        return words ? <WordList json={words} mode="todayexpression" /> : <></>
        // if( words ){
        //     Expression.push(<WordList json={words} />);
        // }
        // return <Sentense info={item} key={i} idx={i} lang={lang}/>
    })
    return (<>
        {Expression}
    </>);
}

export default PageBlogpost;